@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
*{
    font-family: 'Roboto Mono', monospace;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width:375px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.navbar-collapse{
    /* background-color:#282828; */
    padding:15px;
}

.navbar-toggler{
   float:right;
}

nav{
    color:white;
    /* background-color:#282828; */
    margin-bottom:-59px;
    position:absolute;
    z-index:9999;
}

.nav-link{
    cursor:pointer;
    display: inline-block;
  position: relative;
  padding-bottom: 3px;
}

.nav-link:after {
    content: '';
    display: block;
    margin: auto;
    height: 3px;
    width: 0px;
    background: transparent;
    transition: width .3s ease, background-color .3s ease;
  }

  .nav-link:hover:after {
    width: 100%;
    background:white;
  }

.navbar-brand{
    cursor:pointer;
}

.menu-icons{
    margin:0 10px;
    color:white;
    font-size:22px;
    transition:all 200ms;
}

.menu-icons:hover{
    color:#DFDFDF;
    transition:all 200ms;
}

.navbar-brand{
    font-weight:bold;
}

.menu-icons-wrap{
    position:absolute;
    right:10px;
    top:10px;
}

/* The sticky class is added to the header with JS when it reaches its scroll position */
.sticky {
    position: fixed;
    top: 10px;
    z-index:99999;
    right:10px;
    background: linear-gradient;
    background: -webkit-linear-gradient(140deg, rgba(255,66,66,1) 0%, rgba(0,129,255,1) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-color:white;
    transition:all 300ms;
  }

.white-bg{
    position:fixed;
    background-color:white;
    width:180px;
    height:50px;
    right:0;
    top:0;
    border-radius:0 0 0 5px;
    box-shadow:1px 1px 5px black;
    transition:all 300ms;
}

.hero{
    /* background-color:#282828; */
    color:white; 
    min-height:50vh;
}

.hero-row{
    min-height:500px;
    transition:all 500ms;
}

.intro{
    background-color:#F7F7F7;
    color:#282828;
    padding:30px;
    text-align:center;
    transition:all 500ms;
    width:100%;
    border:2px solid black;
    box-shadow:4px 4px 0 black;
}

.hero-title{
    width:100%;
    text-align:center;
    margin:25px 0;
}

.large-header {
    position: relative;
    width: 100%;
    /* background: #282828; */
    background: rgb(255,66,66);
background: linear-gradient(140deg, rgba(255,66,66,1) 0%, rgba(0,129,255,1) 100%);
    overflow: hidden;
    background-size: cover;
    background-position: center center;
    z-index: 1;
    transition:all 500ms;
 }

 @media (max-width: 576px) { 
   .large-header {
    min-height:900px;
   }
}
 
 .main-title {
    position: absolute;
    margin: 0;
    padding: 0;
    text-align: center;
    top: 45%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    width:90%;
    max-width:900px;
 }

 .text-shadow{
    color:white;
   text-shadow:3px 3px 0 black;
 }
 
 .demo .main-title {
    text-transform: uppercase;
    font-size: 4.2em;
    letter-spacing: 0.1em;
 }
 
 .main-title .thin {
    font-weight: 200;
 }
 
 @media only screen and (max-width: 768px) {
    .demo .main-title {
       font-size: 3em;
    }
 }

 .connect-with-me{
    transition:all 500ms;
 }

 .connect-with-me h3{
    margin-top:30px;
    font-size:22px;
 }

 .connect-with-me a{
   font-size:30px;
   color:white;
   padding:0 30px 0 10px;
 }

 .connect-with-me a:hover{
   color:#DFDFDF;
   text-decoration:none;
   transition:all 200ms;
 }

 .connect-icon-text{
    font-size:22px;
    margin-left:10px;
 }
 @media (max-width:767px) and (min-width:576px) {
   .main-title {
       margin-top:100px;
   }
}​
 .divider{
    width:100%;
    margin-top:-200px;
 }



