    transition:all 500ms;
}

.project-description{
    padding:20px;
}

.screenshot-hover{
    -webkit-filter: grayscale(0.5) blur(5px);
            filter: grayscale(0.5) blur(5px);
}

.screenshot-overlay:before{
    content:  "View Live Application";
    position:absolute;
    width:80%;
    text-align:center;
    top:100px;
    /* bottom:45%; */
    left:10%;
    z-index:99;
    font-weight:bold;
    font-size:22px;
    color:black;
    cursor: pointer;
    transition:all 500ms;
    background-color:white;
}

